import React from 'react';
import ReactDOM from 'react-dom';

import './index.css';
import drumData from './drumData';

class DrumPad extends React.Component {
  render() {
    return (
      <div>
        <button className="drum-pad" id={this.props.drum.id} >
          <audio className="clip" id={this.props.drum.keyPressed} onKeyDown={this.props.handleKeyPress}>
            <source src={this.props.drum.audioClip} type="audio/mpeg"/>
            <p>Your browser doesn't support HTML5 audio.</p>
          </audio>
          {this.props.drum.keyPressed}
        </button>
      </div>
    );
  }
}

class App extends React.Component {
  constructor(props) {
    super(props);
    this.handleKeyPress = this.handleKeyPress.bind(this);
  }
  componentDidMount() {
    document.addEventListener("keydown", this.handleKeyPress)
  }
  componentWillUnmount() {
    document.removeEventListener("keydown", this.handleKeyPress)
  }
  handleKeyPress(event) {
    const availKeys = ["Q", "W", "E", "A", "S", "D", "Z", "X", "C"];
    const availKeysCodes = availKeys.map(key => key.charCodeAt());

    if(availKeysCodes.includes(event.keyCode)) {
      let keyPressed = document.getElementById(event.key.toUpperCase());
      keyPressed.play();
    }
  }

  render() {
    const drumSet = drumData.map(item => <DrumPad key={item.id} drum={item} />);

    return (
      <div id="drum-machine">
        <div className="grid">
          {drumSet}
        </div>
        <p id="display">Placeholder text</p>
      </div>
    );
  }
}

ReactDOM.render(<App />, document.getElementById("root"));
